*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body{
  font-size: 18px;
  background-color: #f5f7f5;;
}
h1{
  max-width: 940px;
  font-size: 2.5em;
  font-family: lato,sans-serif;
  font-weight: 700;
  line-height: 1.1;
  margin: 40px 0;
  color: #1b1b32;
}
.App{
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;


}
.contenedor-principal{
  min-height: 600px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

}