.contenedor-testimonio{
    width: 1120px;
    height: 348px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
}

.imagen-testimonio{
    height: 100%;
    width: auto;
}

.contenedor-texto-testimonio{
    text-align: center;
    padding: 40px;
    font-family: Lato,sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
}
.nombre-testimonio{
    font-size: 1.3rem;
}
.nombre-testimonio strong {
    color: #1b1b32;
}

.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}
.cargo-testimonio strong {
    color: #1b1b32;
}
.texto-testimonio{
    font-size: 1.1rem;
    text-align: justify;
}
.texto-testimonio strong{
    color:#1b1b32;

}
